import * as React from 'react';
import { Table, Modal, Avatar, Button, Pagination, Popover } from 'antd';
import { Redirect } from 'react-router-dom';
import axios from 'axios';
import moment from 'moment'
import './index.scss';

interface IState {
    fans_list: ITableItem[],
    spinning: boolean,
    total_fans: number,
    openid_group: any[],
    current_page: number,
    access_token: string
}
interface ITableItem {
    key: number,
    openid: string,
    headimgurl: string,
    nickname: string,
    sex: string,
    addr: string,
    subscribe_time: string
};
const content = (
    <div>
        <p>迫于看傻猴智商受到影响，目前仅支持查询10000条</p>
        <p>智商恢复后优化该页面。。。</p>
    </div>
);
export default class Fans extends React.Component<{ history: any }, IState>{
    constructor(props: { history: any }) {
        super(props);
        const wechatAccount = JSON.parse(localStorage.getItem('wechat_account') as string);
        if (wechatAccount && new Date().getTime() < wechatAccount.expires_time) {
            this.state = {
                fans_list: [],
                spinning: false,
                total_fans: 0,
                current_page: 1,
                openid_group: [],
                access_token: wechatAccount.access_token
            }
        }
    };

    public componentDidMount = () => {
        const wechatAccount = JSON.parse(localStorage.getItem('wechat_account') as string);
        if (wechatAccount && new Date().getTime() < wechatAccount.expires_time) {
            this.getFansListOpenid()
        }
    };

    public render() {
        if (!this.state) {
            return <Redirect to='/auth_manage' />
        } else {
            const { fans_list, spinning, total_fans, current_page } = this.state;
            const columns = [{
                title: 'openid',
                dataIndex: 'openid',
                key: 'openid',
                width: '25%'
            }, {
                title: '头像',
                dataIndex: 'headimgurl',
                key: 'headimgurl',
                render: (text: any, record: any) => (
                    <Avatar src={text} />
                ),
                width: '10%'
            }, {
                title: '昵称',
                dataIndex: 'nickname',
                key: 'nickname',
                width: '15%'
            }, {
                title: '性别',
                dataIndex: 'sex',
                key: 'sex',
                width: '15%'
            }, {
                title: '地区',
                dataIndex: 'addr',
                key: 'addr',
                width: '15%'
            }, {
                title: '关注时间',
                dataIndex: 'subscribe_time',
                key: 'subscribe_time'
            }];
            return (
                <div>
                    <Popover content={content}>
                        <Button type="primary" icon="sync" style={{ marginBottom: 10 }} onClick={this.getFansListOpenid}>同步公众号粉丝({total_fans}粉丝)</Button>
                    </Popover>
                    <Table style={{ marginBottom: 10 }} columns={columns} dataSource={fans_list} pagination={false} loading={{ spinning }} />
                    <Pagination current={current_page} defaultPageSize={100} total={total_fans} onChange={this.onChange} showTotal={(total) => `共有 ${total} 粉丝`} />
                </div>
            )
        }
    };

    private onChange = (page: number) => {
        const { openid_group } = this.state;
        this.getFansListInfo(openid_group[page - 1]);
        this.setState({
            current_page: page,
        })
    };

    private getFansListOpenid = () => {
        this.setState({
            spinning: true
        });
        axios.get(`/api/wechat/cgi-bin/user/get`, {
            params: {
                access_token: this.state.access_token
            }
        }).then(res => {
            if (res.data.errcode) {
                Modal.error({
                    title: '获取粉丝列表',
                    content: res.data.errmsg
                })
                return
            }
            const openidArr = [];
            let splitOpenidArr = [];
            const resArr = res.data.data.openid;
            for (const value of resArr) {
                openidArr.push({
                    openid: value,
                    lang: "zh_CN"
                })
            };
            splitOpenidArr = this.splitArray(openidArr, 100);
            this.getFansListInfo(splitOpenidArr[0]);
            this.setState({
                total_fans: res.data.total,
                openid_group: splitOpenidArr
            });
        }).catch(err => {
            Modal.error({
                title: '获取粉丝列表',
                content: '网络错误！'
            })
        })
    };

    private splitArray = (arr: any[], length: number) => {
        let index = 0;
        const newArray = [];
        while (index < arr.length) {
            newArray.push(arr.slice(index, index += length));
        }
        return newArray;
    };

    private getFansListInfo = (arr: any[]) => {
        axios.post(`/api/wechat/cgi-bin/user/info/batchget?access_token=${this.state.access_token}`, {
            user_list: arr
        }).then(res => {
            if (res.data.errcode) {
                Modal.error({
                    title: '获取粉丝列表',
                    content: res.data.errmsg
                })
                return
            }
            const fansArr = [];
            const resArr = res.data.user_info_list;
            for (let i = 0; i < resArr.length; i++) {
                fansArr.push({
                    key: i + 1,
                    openid: resArr[i].openid,
                    headimgurl: resArr[i].headimgurl,
                    nickname: resArr[i].nickname,
                    sex: resArr[i].sex === 1 ? '男' : '女',
                    addr: resArr[i].country + resArr[i].province + resArr[i].city,
                    subscribe_time: moment.unix(resArr[i].subscribe_time).format("YYYY-MM-DD HH:mm:ss")
                })
            }
            this.setState({
                fans_list: fansArr,
                spinning: false
            })
        }).catch(err => {
            Modal.error({
                title: '获取粉丝列表',
                content: '网络错误！'
            })
        })
    }
}


